<template>
  <div class="">
    <j-advanced-query v-model="query" :fields="fields" @change="doquery" />
    <div>
      <br>
      <h3></h3>
      <div>{{JSON.stringify(fields)}}</div>
      <br>
      <h3></h3>
      <div>{{JSON.stringify(query)}}</div>
      <br>
    </div>
  </div>
</template>
<script>
export default {
  name: 'advanced-query-demo',
  data(){
    return {
      query:{
        "list":[
          {"field":"name","op":"contains","value":""},
          {"field":"age","op":"gt","value":10},
          {"field":"age","op":"lt","value":50},
          {"field":"birthday","op":"eq","value":"2021-12-07"},
          {"field":"six","op":"eq","value":""}
        ],
        "condition":"and"
      },
      fields:[
        {
          'name':'name',
          'label':'',
          'type':'text',
          
        },
        {
          'name':'age',
          'label':'',
          'type':'number',
          'options':{
            min:0
          }
        },
        {
          'name':'birthday',
          'label':'',
          'type':'date',
          'options':{
            'value-format':'yyyy-MM-dd'
          }
        },
        {
          'name':'six',
          'label':'',
          'type':'select',
          'options':{
            'options':[
              {'value':'','label':''},
              {'value':'','label':''}
            ]
          }
        }
      ]
    }
  },
  methods:{
    doquery(){
       this.$notify({
          title: '',
          message: 'change  ',
          type: 'success'
        });
    }
  }
}
</script>