<template>
  <div class="j-advanced-query">
    <el-tooltip effect="dark"
      :disabled="!isUse"
      placement="top-start">
      <div slot="content">
         |
        <el-button type="text" @click="clear"> </el-button>
      </div>
      <el-button
        :icon="isUse?'el-icon-loading':'el-icon-finished'"
        @click="showDialog"
        size="small"
      type="primary">
      
      </el-button>
    </el-tooltip>
    <!--  -->
    <el-dialog :visible.sync="dialogVisible" title="" width="700px" :modal="false">
      <el-form :inline="true">
        <template v-if="isUse">
          <!--  -->
          <el-form-item label="">
            <el-select v-model="value.condition" size="small" style="width:250px">
                <el-option label="AND" value="and"></el-option>
                <el-option label="OR" value="or"></el-option>
            </el-select>
          </el-form-item>
          
            <div class="row" v-for="(item,index) in value.list" :key="index">
              <!--  -->
              <el-form-item>
                <el-select v-model="item.field" size="small" style="width:150px" @change="itemFieldChange(item)" clearable placeholder="">
                  <el-option
                    v-for="item in fields"
                    :key="item.name"
                    :label="item.label"
                    :value="item.name">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-select v-model="item.op" size="small" style="width:100px">
                  <el-option
                    v-for="item in operator"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <!--  -->
                <el-input-number v-if="getFieldType(item.field)=='number'"  v-bind="getFieldProps(item.field)" v-model="item.value" size="small" :controls="false" style="width:180px"></el-input-number>
                <!--  -->
                <el-date-picker v-else-if="getFieldType(item.field)=='date'" v-bind="getFieldProps(item.field)" v-model="item.value" size="small" style="width:180px"></el-date-picker>
                <!--  -->
                <el-select v-else-if="getFieldType(item.field)=='select'" v-bind="getFieldProps(item.field)" v-model="item.value" size="small" style="width:180px">
                  <el-option v-for="item in getFieldProps(item.field).options" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-select>
                <!--  -->
                <el-input v-else v-bind="getFieldProps(item.field)" v-model="item.value" clearable placeholder="" size="small" style="width:180px"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button size="small"  type="primary" plain icon="el-icon-plus" @click="addItem()"></el-button>
                <el-button size="small"  type="danger" plain icon="el-icon-minus" @click="delItem(index)"></el-button>
              </el-form-item>
            </div>
          
        </template>
        <div v-else>
          <el-empty style="padding:0" :image-size="100" description="">
             <el-button type="text" @click="addItem"></el-button>
          </el-empty>
        </div>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="clear"></el-button>
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="commit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'JAdvancedQuery', 
  data () {
    return {
      dialogVisible:false,
      operator:[
        {'label':'','value':'eq'},
        {'label':'','value':'contains'},
        {'label':'..','value':'start'},
        {'label':'..','value':'end'},
        {'label':'...','value':'in'},
        {'label':'','value':'neq'},
        {'label':'','value':'gt'},
        {'label':'','value':'egt'},
        {'label':'','value':'lt'},
        {'label':'','value':'elt'},
      ]
    }

  },
  computed:{
    /**
     * 
     */
    isUse(){
      return this.value && Array.isArray(this.value.list) && this.value.list.length>0
    }
  },

  props: {
    value:{
      default:()=>{},
      type:Object
    },
    fields:{
      default:()=>[],
      type:Array
    }
  }, 

  methods: {
    clear(){
      this.$emit('input',null)
    },
    showDialog(){
      this.dialogVisible = true
    },
    commit(){
      this.dialogVisible = false
      if(this.isUse){
        this.$emit('change')
      }
    },
    /**
     *  
     */
    getFieldProps(field){
      var fieldInfo = this.fields.find(f=>f.name==field)
      if(fieldInfo){
        return fieldInfo['options'] || {}
      }
      else{
        return {}
      } 
    },
    /**
     *  
     */
    getFieldType(field){
      var fieldInfo = this.fields.find(f=>f.name==field)
      if(fieldInfo){
        return fieldInfo.type
      }
      else{
        return 'text'
      } 
    },
    itemFieldChange(item){
      item.value = ''
    },
    addItem(){
      var temp = Object.assign({list:[],condition:'and'},this.value)
      temp.list.push({ field:'', op:'eq', value:''})
      this.$emit('input',temp)
    },
    delItem(index){
      this.value.list.splice(index, 1);
      this.$emit('input',this.value)
    }
  }
}
</script>