<template>
  <div class="jui-sku-container">
    <div class="example">
      <table class="stock-table el-table el-table--border" cellspacing="0" cellpadding="0">
        <thead>
          <tr>
            <th
              v-for="(sp, sp_index) in specification"
              :key="sp_index"
            >
              {{ sp.name }}
            </th>
            <!-- <th>sku key</th> -->
            <th style="width: 160px;"></th>
            <th style="width: 160px;" v-if="showProductStock"></th>
            <th style="width: 160px;" v-if="showProductNo"></th>
            <th style="width: 160px;" v-if="showProductCost"></th>
            <th style="width: 100px;"></th>
          </tr>
        </thead>
        <tbody v-if="value.length>0">
          <tr v-for="(item,index) in value" :key="item.ProductSpec">
            <template v-for="(sp, sp_index) in specification">
              <td  v-if="showTd(sp_index, index)" :rowspan="countSum(sp_index+1)" :key="sp_index">
                {{ getSpecValue(sp_index, index) }}
              </td>
            </template>
            <td>
              <el-input-number
                v-model.number="item.ProductPrice"
                size="small"
                :min="0"
                :precision="2"
                placeholder=""
                :disabled="!item.isUse || disabled"
              >
              </el-input-number>
            </td>
            <td v-if="showProductStock">
              <el-input-number
                v-model.number="item.ProductStock"
                size="small"
                :min="0"
                :controls="false"
                :precision="0"
                placeholder=""
                :disabled="!item.isUse || disabled"
              >
              </el-input-number>
            </td>
            <td v-if="showProductNo">
              <el-input
                v-model="item.ProductNo"
                size="small"
                type="text"
                :disabled="!item.isUse || disabled"
                placeholder=""
              >
              </el-input>
            </td>
            <td v-if="showProductCost">
              <el-input-number
                v-model.number="item.ProductCost"
                size="small"
                :min="0"
                :precision="2"
                :disabled="!item.isUse || disabled"
              >
              </el-input-number>
            </td>
            <td>
              <el-switch v-model="item.isUse" :disabled="disabled"></el-switch>
            </td>
          </tr>
          <tr>
            <td colspan="8" class="wh-foot">
              <span class="label"></span>
              <div v-if="isSetListShow" class="set-list">
                <span class="set-item" @click="openBatch('ProductPrice')"></span>
                <span v-if="showProductStock" class="set-item" @click="openBatch('ProductStock')"></span>
                <span v-if="showProductCost" class="set-item" @click="openBatch('ProductCost')"></span>
              </div>
              <div v-else class="set-form">
                <el-input-number v-model.number="batchValue" :controls="false" size="mini" :min="0" :precision="currentType=='ProductStock'?0:2" placeholder=""></el-input-number>
                <i class="set-btn blue el-icon-check" @click="setBatch"></i>
                <i class="set-btn red el-icon-close" @click="cancelBatch"></i>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div v-if="isShowCode" class="example">
      <textarea class="code-area" :value="JSON.stringify(value)"></textarea>
    </div>
  </div>
</template>

<script>
// 
function descartes(list) {
  return Array.prototype.reduce.call(list, function(a, b) {
    var ret = []
    a.forEach(function(a) {
      b.forEach(function(b) {
        ret.push(a.concat([b]))
      })
    })
    return ret
  }, [[]])
}

export default {
  name: 'JSkuTable',
  props: {
    defaultPrice:{
      type: Number,
      default: 1
    },
    // json
    isShowCode: {
      type: Boolean,
      default: false
    },
    //  
    showProductNo:{
      type: Boolean,
      default: true
    },
    // 
    showProductCost:{
      type: Boolean,
      default: true
    },
    // 
    showProductStock:{
      type: Boolean,
      default: true
    },
    specification: { // 
      type: Array,
      default: function() { return [] }
    },
    disabled:{ // 
      type: Boolean,
      default: false
    },
    value: {
      type: Array,
      default: function() { return [] }
    }
  },
  data() {
    return {
      // 
      defaultSkuNo: 'PRONO_',
      // 
      isSetListShow: true,
      batchValue: '', // 
      currentType: '' // 
    }
  },
  watch: {
    specification: {
      handler() {
        this.bulidSKUS()
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    // sku 
    bulidSKUS() {
      const spValues = []
      this.specification.forEach(sp => {
        if (sp.value.length > 0)spValues.push(sp.value)
      })

      const descar = descartes(spValues)

      const netSKU = []
      descar.forEach(key => {
        if (key.length === 0) return
        const oldSp = this.getOldOrCreatData(JSON.stringify(key)) // 
        netSKU.push(oldSp)
      })

      //     
      this.value.length = 0
      this.value.push(...netSKU)
      this.$emit('input', this.value)
    },
    //    
    getOldOrCreatData(spec_key) {
      let data = {
        ProductCost: this.defaultPrice,
        ProductId: 0,
        ProductNo: 1,
        ProductPrice: this.defaultPrice,
        ProductSpec: spec_key,
        ProductStock: 999,
        isUse: true
      }
      this.value.forEach(res => {
        if (res.ProductSpec === spec_key) { data = res }
      })
      return data
    },
    // 
    getSpecValue(specIndex, index) {
      // 
      const currentValues = this.specification[specIndex].value
      let indexCopy

      // 
      if (this.specification[specIndex + 1] && this.specification[specIndex + 1].value.length) {
        indexCopy = index / this.countSum(specIndex + 1)
      } else {
        indexCopy = index
      }

      const i = Math.floor(indexCopy % currentValues.length)

      if (i.toString() !== 'NaN') {
        return currentValues[i]
      } else {
        return ''
      }
    },
    //    
    countSum(specIndex) {
      let num = 1
      this.specification.forEach((item, index) => {
        if (index >= specIndex && item.value.length) {
          num *= item.value.length
        }
      })
      return num
    },
    // td
    showTd(specIndex, index) {
      // 
      if (!this.specification[specIndex]) {
        return false
      } else if (index % this.countSum(specIndex + 1) === 0) {
        return true
      } else {
        return false
      }
    },
    //   
    // 
    openBatch(type) {
      if(this.disabled) return // 
      this.currentType = type
      this.isSetListShow = false
    },
    // 
    setBatch() {
      if (typeof this.batchValue === 'string') {
        this.$message({
          type: 'warning',
          message: ''
        })
        return
      }
      this.value.forEach(item => {
        if (item.isUse) {
          item[this.currentType] = this.batchValue
        }
      })

      this.cancelBatch()
    },
    // 
    cancelBatch() {
      this.batchValue = ''
      this.currentType = ''
      this.isSetListShow = true
    }
  }
}
</script>